<!--  -->
<template>
  <section class="fullScreen-page" v-if="isshow">
    <aside class="leftPanel">
      <div class="panelHeader">
        <span class="btn" @click="$emit('update:isshow', false)">关闭弹窗</span>
        <span class="btn">复制代码</span>
        <span class="btn" @click="runCode">运行代码</span>
      </div>
      <div class="codemirrorBox">
        <codemirror
          ref="editor"
          id="editor"
          v-model="mirror_code"
          :options="options"
          @beforeChange="beforeChange"
          @beforeCursorEnter="handleBeforeCursorEnter"
        ></codemirror>
      </div>
    </aside>
    <main class="rightPanel">
      <!-- <div class="preview" v-html="compiled"></div>
      <iframe id="frame" frameborder="0"></iframe> -->
      <div id="preview">预览</div>
      <!-- <div v-if="compiledComponent">${compiledComponent}</div> -->
      <!-- <my-component></my-component> -->
      <div style="height: 300px" ref="componentPreview">预览2</div>
      <!-- <component :is="previewComponent" :key="componentKey"></component>
      <dynamic-component /> -->
    </main>
  </section>
</template>

<script>
  import Vue from 'vue';
  import * as echarts from 'echarts';
  import { codemirror } from 'vue-codemirror';
  // import VueCodeMirror from 'vue-codemirror'; // 这种方式引入 ,组件注册见下方。
  // import * as CodeMirror from 'codemirror/lib/codemirror'
  import { compile, parseComponent, parse, compileToFunctions, createComponent } from 'vue-template-compiler';
  import compiler from 'vue-template-compiler';
  // 核心样式
  import 'codemirror/lib/codemirror.css';
  // 引入主题后还需要在 options 中指定主题才会生效
  import 'codemirror/mode/javascript/javascript.js';
  import 'codemirror/mode/css/css.js';
  import 'codemirror/mode/vue/vue.js';
  import './setting.js';
  import './utils/codemirrorVueParse.js';
  // import './mode/vueMode.js';

  export default {
    components: {
      codemirror
      // codemirror: VueCodemirror.codemirror
    },
    props: {
      isshow: {
        type: Boolean,
        default: false
      },
      codecontent: {
        // 编辑器的值
        type: String,
        default: 'hello world'
      }
    },
    data() {
      return {
        code: 'Hello World!', // 编辑器绑定的值
        // 默认配置
        options: {
          tabSize: 2, // tab
          styleActiveLine: true, // 高亮选中行
          lineNumbers: true, // 显示行号
          styleSelectedText: true,
          line: true,
          foldGutter: true, // 块槽
          // readOnly: true,
          // readOnly: (line) => {
          //   console.log('line', line);
          //   // 判断当前行是否为单行注释（以 // 开头）
          //   return /^\s*\/\//.test(line.text);
          // },
          // 禁用注释行
          comment: false,
          gutters: ['CodeMirror-linenumbers', 'CodeMirror-foldgutter', 'disabled-lines'],
          highlightSelectionMatches: { showToken: /\w/, annotateScrollbar: true }, // 可以启用该选项来突出显示当前选中的内容的所有实例
          // mode: 'text/html',
          // mode: 'text/javascript',
          // mode: 'text/x-vue', // 这个vue模式，OK
          // mode: 'htmlmixed',
          mode: 'vue',
          // hint.js options
          hintOptions: {
            // 当匹配只有一项的时候是否自动补全
            completeSingle: false
          },
          // 快捷键 可提供三种模式 sublime、emacs、vim
          keyMap: 'sublime',
          matchBrackets: true,
          showCursorWhenSelecting: true,
          theme: 'base16-light', // 主题  'monokai' 'solarized light'  'base16-light'
          autoCloseBrackets: true, //自动补全括号
          extraKeys: { Ctrl: 'autocomplete' } // 可以用于为编辑器指定额外的键绑定，以及keyMap定义的键绑定
        },
        mirror_code: '',
        compiledComponent: null,
        // 11
        previewComponent: null,
        componentKey: 0 // 动态组件的键值
      };
    },
    computed: {
      compiled() {
        const component = compile(this.codecontent).code;
        return component;
      }
    },
    watch: {
      code: {
        handler: function(newval) {
          const frame = document.querySelector('#frame');
          frame.contentWindow.document.open();
          frame.contentWindow.document.write(newval);
        }
      },
      codecontent: {
        handler(val) {
          this.mirror_code = val;
          // this.codeChange10E(val);
        },
        immediate: true
      },
      mirror_code: {
        handler(val) {
          this.codeChange10E(val);
        },
        immediate: true
      }
    },
    mounted() {
      this.$nextTick(() => {
        this.disabledCodeMirror();
      });
    },
    methods: {
      disabledCodeMirror() {
        const editor = this.$refs.editor.codemirror;
        const disabledLines = [2, 5, 8]; // 禁用第 2、5、8 行
        editor.addLineClass(2, 'background', 'disabled-line');
        editor.addLineClass(5, 'background', 'disabled-line');
        editor.addLineClass(8, 'background', 'disabled-line');
        editor.setGutterMarker(2, 'disabled-lines', this.createMarker());
        editor.setGutterMarker(5, 'disabled-lines', this.createMarker());
        editor.setGutterMarker(8, 'disabled-lines', this.createMarker());
        //
        editor.on('renderLine', (cm, line, elt) => {
          const text = line.text.trim();
          if (text.startsWith('//')) {
            // console.log('cm', cm);
            // console.log('cm.addLineClass', cm.addLineClass);
            // console.log(line, elt, text);
            // const handle = cm.getLineHandle(line.lineNo());
            // cm.removeLineClass(handle, 'background', 'cm-comment');
            // cm.addLineClass(handle, 'background', 'cm-comment');
            // line && cm.addLineClass(line, 'background', 'cm-comment');
            // editor.addLineClass(line, 'start', 'cm-comment');
          }
        });
      },
      createMarker() {
        const marker = document.createElement('div');
        marker.className = 'disabled-line-marker';
        return marker;
      },
      beforeChange(instance, change) {
        console.log('instance', instance);
        console.log('change', change);

        const disabledLines = [2, 5, 8]; // 禁用第 2、5、8 行
        const from = change.from.line;
        const to = change.to.line;
        for (let i = from; i <= to; i++) {
          const curLine = instance.doc.getLine(i); // 获取当前行的内容
          console.log('curLine', curLine);
          if (disabledLines.includes(i)) {
            change.cancel();
            break;
          }
        }
      },
      handleBeforeCursorEnter(editor, event) {
        const cursor = editor.getCursor();
        const line = editor.getLine(cursor.line);

        if (line.match(/\b\/\/.*$/)) {
          console.log('进入了');
          // 如果当前行以双斜杠开头（即包含单行注释），则取消光标进入该行的操作
          event.preventDefault();
        }
      },

      runCode() {
        // this.previewComponent = { template: this.codecontent };
        // // 使用 try-catch 来捕捉编译时可能出现的错误
        // try {
        //   eval(`(${this.codecontent})`);
        //   this.data.push(Math.floor(Math.random() * 100));
        // } catch (e) {
        //   console.error(e);
        // }
        this.codeChange13(this.codecontent);
      },
      codeChange(content) {
        console.log('compiler', compiler);
        const { source, template, script, styles } = parseComponent(content);
        console.log('解析出template、script、style', parseComponent(content));
        const { render, staticRenderFns } = compile(template);
        // const { render, staticRenderFns, options } = Vue.compile(content);
        console.log('compile(template)', compile(template));
        // console.log('render', Vue.compile(content));
        // 使用Vue.extend创建一个组件构造器
        const ComponentConstructor = Vue.extend({
          render,
          staticRenderFns
        });

        // 实例化一个组件，并挂载到DOM中
        this.$nextTick(() => {
          const instance = new ComponentConstructor();
          instance.$mount('#preview');
          // new ComponentConstructor().$mount('#preview');
        });
      },
      codeChange2(content) {
        // const { render, staticRenderFns } = compile(content);
        // console.log('render', render);
        const template = '<div>{{ message }}</div>';
        const { render } = compile(template);
        this.$nextTick(() => {
          new Vue({
            el: '#preview',
            data: {
              eleId: `eleId_${Math.random()
                .toString()
                .substring(3, 6) + Date.now().toString(36)}`,
              message: 'Hello, Vue!'
            },
            render: render
          });
        });
      },
      codeChange3(content) {
        const component = compileToFunctions(content);
        const vm = new Vue({
          render: component.render,
          staticRenderFns: component.staticRenderFns
        });
        // this.$nextTick(() => {
        //   const instance = new vm();
        //   instance.$mount('#preview');
        //   // new ComponentConstructor().$mount('#preview');
        // });
        this.$nextTick(() => {
          this.compiledComponent = vm.$mount('#preview').$el.outerHTML;
        });
      },
      codeChange4(content) {
        const { source, template, script, styles } = parseComponent(content);
        const { render, staticRenderFns } = compile(template);
        const vm = new Vue({
          render: render,
          staticRenderFns: staticRenderFns
        });
        // this.$nextTick(() => {
        //   const instance = new vm();
        //   instance.$mount('#preview');
        //   // new ComponentConstructor().$mount('#preview');
        // });
        this.$nextTick(() => {
          this.compiledComponent = vm.$mount('#preview');
        });
      },
      codeChange5(content) {
        const compiled = compile(content);
        new Vue({
          render: h => h(compiled)
        }).$mount('#preview');
      },
      codeChange6(content) {
        console.log('compiler', compiler);
        console.log('content', content);
        const { template, script, styles } = parseComponent(content);
        console.log('parseComponent', parseComponent(content));

        const compiled = compile(template.content);
        const Component = createComponent(compiled, script.content);
        console.log('Component', Component);

        new Vue({
          components: {
            'preview-component': Component
          },
          template: `
            <div>
              <div v-html="styles"></div>
              <preview-component></preview-component>
            </div>
          `
        }).$mount('#preview');
      },
      codeChange7(content) {
        const { template, script, styles } = parseComponent(content);
        console.log('parseComponent', parseComponent(content));
        const MyComponent = Vue.extend({
          template: template,
          // props: eval(`({ ${scriptCode.match(/props\s*:\s*({[\s\S]*})/)[1]} })`),
          data() {
            // 解析 scriptCode，返回组件的 data 对象
            return eval(`({ ${script.content.match(/data\s*\(\)\s*{\s*(\S[\s\S]*\S)\s*}/)[1]} })`);
          },
          // methods: eval(`({ ${script.content.match(/methods\s*:\s*({[\s\S]*})/)[1]} })`),
          style: eval(`({ ${styles} })`)
        });

        const vm = new MyComponent({
          el: '#preview'
        });
      },
      codeChange8(content) {
        const { template, script, styles } = parseComponent(content);
        console.log('parseComponent', parseComponent(content));
        const templateCode = template.content;
        const compiledTemplate = Vue.compile(templateCode);

        const scriptCode = script.content;
        const scriptDataMatch = scriptCode.match(/data\s*\(\)\s*\{\s*return\s*({[\s\S]*})\s*\}/);
        const scriptMethodsMatch = scriptCode.match(/methods\s*:\s*({[\s\S]*})/);
        const data = scriptDataMatch ? new Function(`return ${scriptDataMatch[1]}`)() : null;
        const methods = scriptMethodsMatch ? new Function(`return ${scriptMethodsMatch[1]}`)() : null;

        const styleCode = styles;

        const componentObj = {
          data,
          methods,
          render: compiledTemplate.render
        };
        if (styleCode) {
          componentObj.styles = [styleCode];
        }

        const vm = new Vue({
          el: '#app',
          components: {
            'my-component': componentObj
          }
        });
      },
      codeChange9(content) {
        const { template, script, styles } = parseComponent(content);
        console.log('parseComponent', parseComponent(content));

        let options = {
          template: template.content,
          script: script.content
        };
        let component = Vue.component('preview-component', options);
        console.log('component', component);
        let vm = new Vue(component).$mount();
        console.log('vm', vm);
        this.$nextTick(() => {
          this.$refs.componentPreview.appendChild(vm.$el);
        });
      },
      // 这个有一点改进1
      codeChange10(content) {
        const { template, script, styles } = parseComponent(content);
        console.log('parseComponent', parseComponent(content));

        let options = {
          template: template.content,
          script: script.content
        };

        let component = Vue.extend({
          template: options.template,
          data() {
            return {
              eleId: `eleId_${Math.random()
                .toString()
                .substring(3, 6) + Date.now().toString(36)}`,
              msg: 'hello'
              // 在这里定义data选项
            };
          },
          mounted() {
            // this.$nextTick(() => {
            //   this.initEchart();
            // });
            setTimeout(() => {
              this.initEchart();
            }, 2000);
          },
          methods: {
            initEchart() {
              console.log('eleId', this.eleId);
              console.log('document.getElementById(this.eleId)', document.getElementById(this.eleId));
              const echarts = this.$echart;
              let myChart = echarts.init(document.getElementById(this.eleId));
              const option = {
                grid: {
                  top: 0,
                  bottom: 0,
                  left: 0
                },
                xAxis: {
                  type: 'category',
                  data: ['Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat', 'Sun']
                },
                yAxis: {
                  type: 'value'
                },
                series: [
                  {
                    data: [150, 230, 224, 218, 135, 147, 260],
                    type: 'line'
                  }
                ]
              };
              myChart.setOption(option);
              window.addEventListener('resize', () => {
                myChart.resize();
              });
            }
          }
        });
        let vm = new component().$mount();
        console.log('vm', vm);
        this.$nextTick(() => {
          this.$refs.componentPreview.appendChild(vm.$el);
        });
      },
      // 基于 codeChange10 改进
      codeChange10A(content) {
        const { template, script, styles } = parseComponent(content);
        console.log('parseComponent', parseComponent(content));

        let options = {
          template: template.content,
          script: script.content
        };

        const dataRegExp = /data\(\)\s*{\s*return\s*{\s*(.*)\s*};\s*}/s;
        const dataMatch = script.content.match(dataRegExp);
        const dataContent = dataMatch ? dataMatch[1].trim() : '';
        console.log('dataContent', dataContent);

        // const methodsRegExp = /methods:\s*{\s*([\s\S]*?)\s*}\s*$/; // 解析的为空
        // const methodsRegExp = /methods:\s*{\s*([\s\S]*?)\s*}\s*/; // 解析的只有部分内容？
        // const methodsRegExp = /methods:\s*{\s*([\s\S]*?)\s*}\s*/; // 解析一部分
        const methodsRegExp = /methods:\s*{\s*([\s\S]*?)\s*}\s*\)/; // 少了末尾的 )}}，这个最接近了
        // const methodsRegExp = /methods:\s*{\s*([\s\S]*?)\s*}\s*/;
        const methodsMatch = script.content.match(methodsRegExp);
        const methodsContent = methodsMatch ? methodsMatch[1].trim() : '';
        console.log('methodsContent', methodsContent);

        // 匹配 data() 函数体
        // const dataMatch = script.content.trim().match(/data\s*\(\s*\)\s*{\s*return\s*({[^]+})\s*}\s*/);
        // console.log('dataMatch', dataMatch);
        // const dataObj = dataMatch ? JSON.parse(dataMatch[1]) : {};
        // console.log('dataObj', dataObj);

        // 匹配 methods 函数体
        // const methodsMatch = script.content.trim().match(/methods\s*:\s*{([^]+)}\s*/);
        // const methodsObj = methodsMatch ? eval(`({${methodsMatch[1]}})`) : {};
        // console.log('methodsObj', methodsObj);

        //

        let component = Vue.extend({
          template: options.template,
          data() {
            return {
              dataContent
            };
          },
          mounted() {
            setTimeout(() => {
              this.initEchart();
            }, 2000);
          },
          methods: {
            methodsContent
          }
        });
        let vm = new component().$mount();
        console.log('vm', vm);
        this.$nextTick(() => {
          this.$refs.componentPreview.appendChild(vm.$el);
        });
      },
      codeChange10B(content) {
        const { template, script, styles } = parseComponent(content);
        console.log('parseComponent', parseComponent(content));

        let options = {
          template: template.content,
          script: script.content
        };

        const regex = /export default \{([\s\S]*)\};/;
        const match = regex.exec(script.content);
        const vueOptions = match[1];
        console.log('vueOptions', vueOptions);
        //
        const str = 'export default { prop:{}}';
        console.log('eval', eval(str));
        console.log('vueOptions.props', vueOptions.props);
        // console.log('JSON.parse(vueOptions)', JSON.parse(vueOptions));
        // console.log('props', JSON.parse(vueOptions).props);

        //

        let component = Vue.extend({
          template: options.template,
          ...vueOptions
        });
        let vm = new component().$mount();
        console.log('vm', vm);
        this.$nextTick(() => {
          this.$refs.componentPreview.appendChild(vm.$el);
        });
      },
      codeChange10C(content) {
        const { template, script, styles } = parseComponent(content);
        console.log('parseComponent', parseComponent(content));

        let options = {
          template: template.content,
          script: script.content
        };

        // 将字符串解析为一个 Vue 组件构造函数
        const componentCtor = Vue.extend({
          template: script.content,
          components: { echarts }
        });
        const root = new Vue({
          el: '#app',
          components: {
            temp: componentCtor
          },
          template: '<temp/>'
        });

        const exportObj = root.$children[0].$options.exports.default;

        console.log('exportObj', exportObj);

        //

        // let component = Vue.extend({
        //   template: options.template,
        //   ...vueOptions
        // });
        // let vm = new component().$mount();
        // console.log('vm', vm);
        // this.$nextTick(() => {
        //   this.$refs.componentPreview.appendChild(vm.$el);
        // });
      },
      codeChange10D(content) {
        const { template, script, styles } = parseComponent(content);
        console.log('parseComponent', parseComponent(content));

        let options = {
          template: template.content,
          script: script.content
        };

        const { props = {}, methods = {} } = script.attrs;
        console.log('props:', props); // 输出解析出来的 props
        console.log('methods:', methods); // 输出解析出来的 methods

        // const reg = /initEchart\(\)\s*{\s*((?:.|\s)*)}/;
        // const matches = script.content.match(reg);
        // console.log('matches[1]', matches[1]); // 输出 initEchart 方法的完整内容

        // const regex = /initEchart\s*\(\s*\)\s*\{\s*([\s\S]*)\s*\}/;
        // const result = script.content.match(regex)[1];
        // console.log('result', result);

        // const reg = /initEchart\s*\(\)\s*{\s*let myChart[^}]*}\s*const option \\=\s*{([^}]*)}/;
        // const match = script.content.match(reg);
        // const optionContent = match && match[1];
        // console.log('optionContent,', optionContent);

        const propsRegExp = /props:\s*{([\s\S]*?)},/;
        const initEchartRegExp = /methods:\s*{([\s\S]*?)}/;

        const propsMatch = script.content.match(propsRegExp)[1];
        const initEchartMatch = script.content.match(initEchartRegExp);

        if (propsMatch) {
          console.log('propsMatch', propsMatch);
          console.log('eval(`(${propsMatch})`)', new Function(`return ${propsMatch}`)());
        }

        if (initEchartMatch) {
          console.log('initEchartMatch', initEchartMatch[1]);
        }

        const initEchartStr = `
          const echarts = this.$echart;
          let myChart = echarts.init(document.getElementById(this.eleId));
          const option = {
            grid: {
              top: 0,
              bottom: 0,
              left: 0
            },
            xAxis: {
              type: 'category',
              data: this.echdata.xData
            },
            yAxis: {
              type: 'value'
            },
            series: [
              {
                data: this.echdata.yData,
                type: 'line'
              }
            ]
          };
          myChart.setOption(option);
          window.addEventListener('resize', () => {
            myChart.resize();
          });
        `;

        let component = Vue.extend({
          template: options.template,
          props: eval(`(${propsMatch})`),
          data() {
            return {
              eleId: `eleId_${Math.random()
                .toString()
                .substring(3, 6) + Date.now().toString(36)}`,
              msg: 'hello'
              // 在这里定义data选项
            };
          },
          mounted() {
            // this.$nextTick(() => {
            //   this.initEchart();
            // });
            setTimeout(() => {
              this.initEchart();
            }, 2000);
          },
          methods: {
            initEchart() {
              eval(initEchartStr);
            }
          }
        });
        let vm = new component().$mount();
        console.log('vm', vm);
        this.$nextTick(() => {
          this.$refs.componentPreview.appendChild(vm.$el);
        });
      },
      codeChange10E(content) {
        const { template, script, styles } = parseComponent(content);
        console.log('parseComponent', parseComponent(content));
        const { render, staticRenderFns } = compile(template.content);
        console.log('template', compile(template.content));
        console.log('script', compile(script.content));
        let options = {
          template: template.content,
          script: script.content
        };

        let defaultEchdata = {} || [];
        getdefaultEchdata();
        function getdefaultEchdata() {
          const regex = /\/\/tag1-start([\s\S]*?)\/\/tag1-end/;
          const match = content.match(regex);

          if (match) {
            const res = match[1];
            // console.log('内容2', res.trim());
            const fooStr = `
              function foo(){
                ${res}
                return defaultEchdata;
              }
            `;
            // console.log(22, eval(res.trim()));
            // console.log('2222', eval(`${res.trim()}`));
            defaultEchdata = eval(`(${fooStr})`)();
            // console.log(444, defaultEchdata);
          } else {
            alert('加载失败， 刷新页面后重试');
          }
        }

        let initEchartStr = '';
        getInitEchartFoo();
        function getInitEchartFoo() {
          const regex = /\/\/tag2-start([\s\S]*?)\/\/tag2-end/;
          const match = content.match(regex);
          if (match) {
            const res = match[1];
            console.log('tag2', res);
            let myres = `
              const echarts = this.$echart;
              ${res.trim()}
            `;
            initEchartStr = myres;
          } else {
            alert('加载失败， 刷新页面后重试');
          }
        }

        let component = Vue.extend({
          template: template.content,
          props: {
            echdata: {
              type: Object,
              default: () => defaultEchdata
            }
          },
          data() {
            return {
              eleId: `eleId_${Math.random()
                .toString()
                .substring(3, 6) + Date.now().toString(36)}`
            };
          },
          mounted() {
            // this.$nextTick(() => {
            //   this.initEchart();
            // });
            setTimeout(() => {
              this.initEchart();
            }, 1000);
          },
          methods: {
            initEchart() {
              eval(initEchartStr);
            }
          }
        });
        let vm = new component().$mount();
        // console.log('vm', vm);
        this.$nextTick(() => {
          this.$refs.componentPreview.innerHTML = '';
          this.$refs.componentPreview.appendChild(vm.$el);
        });
      },
      codeChange11(content) {
        // const { template, script, styles } = parseComponent(content);
        const parsedComponent = parseComponent(content);
        const template = parsedComponent.template.content.trim();
        const { render, staticRenderFns } = compile(template);
        console.log('compile', compile);
        this.previewComponent = new Vue({
          render: h => h({ render, staticRenderFns })
        }).$mount();
      },
      codeChange12(content) {
        const res = compile(content);

        // 定义组件名称，避免重名
        const componentName = `preview-component-${Date.now()}`;
        const componentCode = `${res.render.code};export default {name:'${componentName}'}`;
        const component = Vue.component(componentName, {
          render: new Function(componentCode)
        });

        // 返回组件构造函数
        this.previewComponent = component;
        // return component;
      },
      codeChange13(content) {
        const { template, script, styles } = parseComponent(content);
        const { render, staticRenderFns } = compile(template.content.trim());
        const compOptions = {
          render,
          staticRenderFns
        };

        // 注册组件
        Vue.component('dynamic-component', compOptions);
      }
      //
    }
  };
</script>

