<!--  -->
<template>
  <section class="fullScreen-page" v-if="isshow">
    <aside class="leftPanel">
      <div class="panelHeader">
        <span class="btn" @click="$emit('update:isshow', false)">关闭弹窗</span>
        <span class="btn" v-clipboard:copy="mirror_code" v-clipboard:success="clipboardSuccess">复制代码</span>
        <span class="btn" @click="runCode">运行代码</span>
      </div>
      <div class="codemirrorBox">
        <codemirror ref="editor" id="editor" v-model="mirror_code" :options="options" @beforeChange="beforeChange"></codemirror>
      </div>
    </aside>
    <main class="rightPanel">
      <div class="previewBox" ref="componentPreview">预览2</div>
    </main>
  </section>
</template>

<script>
  import Vue from 'vue';
  // import * as echarts from "echarts";
  import { codemirror } from 'vue-codemirror';
  import { compile, parseComponent } from 'vue-template-compiler';
  import { pop } from '@/utils/pop/index.js';
  // import compiler from "vue-template-compiler";
  // 核心样式
  import 'codemirror/lib/codemirror.css';
  // 引入主题后还需要在 options 中指定主题才会生效
  import 'codemirror/mode/javascript/javascript.js';
  import 'codemirror/mode/css/css.js';
  import 'codemirror/mode/vue/vue.js';
  import './setting.js';
  // import './utils/codemirrorVueParse.js'

  export default {
    components: {
      codemirror
    },
    props: {
      isshow: {
        type: Boolean,
        default: false
      },
      codecontent: {
        // 编辑器的值
        type: String,
        default: 'hello world'
      }
    },
    data() {
      return {
        // 默认配置
        options: {
          tabSize: 2, // tab
          styleActiveLine: true, // 高亮选中行
          lineNumbers: true, // 显示行号
          styleSelectedText: true,
          line: true,
          foldGutter: true, // 块槽
          // readOnly: true,
          // readOnly: (line) => {
          //   console.log('line', line);
          //   // 判断当前行是否为单行注释（以 // 开头）
          //   return /^\s*\/\//.test(line.text);
          // },
          // 禁用注释行
          comment: false,
          gutters: ['CodeMirror-linenumbers', 'CodeMirror-foldgutter', 'disabled-lines'],
          highlightSelectionMatches: { showToken: /\w/, annotateScrollbar: true }, // 可以启用该选项来突出显示当前选中的内容的所有实例
          // mode: 'text/html',
          // mode: 'text/javascript',
          // mode: 'text/x-vue', // 这个vue模式，OK
          // mode: 'htmlmixed',
          mode: 'vue',
          // hint.js options
          hintOptions: {
            // 当匹配只有一项的时候是否自动补全
            completeSingle: false
          },
          // 快捷键 可提供三种模式 sublime、emacs、vim
          keyMap: 'sublime',
          matchBrackets: true,
          showCursorWhenSelecting: true,
          theme: 'base16-light', // 主题  'monokai' 'solarized light'  'base16-light'
          autoCloseBrackets: true, //自动补全括号
          extraKeys: { Ctrl: 'autocomplete' } // 可以用于为编辑器指定额外的键绑定，以及keyMap定义的键绑定
        },
        mirror_code: '',
        commentLines: [] // 含有注释的行号
      };
    },
    computed: {
      compiled() {
        const component = compile(this.codecontent).code;
        return component;
      }
    },
    watch: {
      codecontent: {
        handler(val) {
          this.mirror_code = val;
        },
        immediate: true
      },
      mirror_code: {
        handler(val) {
          this.codeChange10E(val);
          this.disabledCodeMirror();
        },
        immediate: true
      }
    },
    mounted() {
      this.$nextTick(() => {
        this.disabledCodeMirror();
      });
    },
    methods: {
      disabledCodeMirror() {
        const editor = this.$refs.editor?.codemirror;
        if (!editor) return;
        const commentLines = [];
        editor.doc.eachLine(line => {
          if (line.text.trim().startsWith('//tag')) {
            commentLines.push(line.lineNo());
            // commentLines.push(line.lineNo() - 1);
          }
        });
        this.commentLines = commentLines;
        // console.log('Comment lines:', commentLines); // 获取所有的注释行
        commentLines.map(i => {
          editor.addLineClass(i, 'background', 'disabled-line');
          editor.setGutterMarker(i, 'disabled-lines', this.createMarker());
        });
        //
        // const editor = this.$refs.editor.codemirror;
        // const disabledLines = [2, 6, 8]; // 禁用第 2、5、8 行
        // editor.addLineClass(2, 'background', 'disabled-line');
        // editor.addLineClass(6, 'background', 'disabled-line');
        // editor.addLineClass(8, 'background', 'disabled-line');
        // editor.setGutterMarker(2, 'disabled-lines', this.createMarker());
        // editor.setGutterMarker(6, 'disabled-lines', this.createMarker());
        // editor.setGutterMarker(8, 'disabled-lines', this.createMarker());
      },
      createMarker() {
        const marker = document.createElement('div');
        marker.className = 'disabled-line-marker';
        return marker;
      },
      beforeChange(instance, change) {
        // const disabledLines = [2, 6, 8]; // 禁用第 2、6、8 行
        const disabledLines = this.commentLines; // 禁用 xxx 行
        const from = change.from.line;
        const to = change.to.line;
        for (let i = from; i <= to; i++) {
          //   const curLine = instance.doc.getLine(i); // 获取当前行的内容
          //   console.log('curLine', curLine);
          if (disabledLines.includes(i)) {
            change.cancel();
            break;
          }
        }
      },
      // 复制
      clipboardSuccess() {
        pop();
      },
      runCode() {},
      codeChange10E(content) {
        const { template, script, styles } = parseComponent(content);
        const { render, staticRenderFns } = compile(template.content);
        let options = {
          template: template.content,
          script: script.content
        };

        // echart默认数据
        let defaultEchdata = {} || [];
        _getdefaultEchdata();
        function _getdefaultEchdata() {
          const regex = /\/\/tag1-start([\s\S]*?)\/\/tag1-end/;
          const match = content.match(regex);
          if (match) {
            const res = match[1];
            const fooStr = `
               function foo(){
                  ${res}
                  return defaultEchdata;
                }
              `;
            defaultEchdata = eval(`(${fooStr})`)();
          } else {
            alert('加载失败， 刷新页面后重试');
          }
        }

        // 标题
        let defaultTitle = '';
        _getdefaultTitle();
        function _getdefaultTitle() {
          const regex = /\/\/tag1B-start([\s\S]*?)\/\/tag1B-end/;
          const match = content.match(regex);
          if (match) {
            const res = match[1];
            const fooStr = `
               function foo(){
                  ${res}
                  return defaultTitle;
                }
              `;
            defaultTitle = eval(`(${fooStr})`)();
          } else {
            alert('加载失败， 刷新页面后重试');
          }
        }

        // echart 初始化
        let initEchartStr = '';
        _getInitEchartFoo();
        function _getInitEchartFoo() {
          const regex = /\/\/tag2-start([\s\S]*?)\/\/tag2-end/;
          const match = content.match(regex);
          if (match) {
            const res = match[1];
            let myres = `
                const echarts = this.$echarts;
                ${res.trim()}
            `;
            initEchartStr = myres;
          } else {
            alert('加载失败， 刷新页面后重试');
          }
        }

        let component = Vue.extend({
          template: template.content,
          props: {
            echdata: {
              type: [Object, Array],
              default: () => defaultEchdata
            },
            titleText: {
              type: String,
              default: defaultTitle
            }
          },
          data() {
            return {
              eleId: `eleId_${Math.random()
                .toString()
                .substring(3, 6) + Date.now().toString(36)}`
            };
          },
          mounted() {
            // this.$nextTick(() => {
            //   this.initEchart();
            // });
            setTimeout(() => {
              this.initEchart();
            }, 1000);
          },
          methods: {
            initEchart() {
              try {
                eval(initEchartStr);
              } catch {
                alert('请刷新页面重试');
              }
            }
          }
        });
        let vm = new component().$mount();
        // console.log('vm', vm);
        this.$nextTick(() => {
          this.$refs.componentPreview.innerHTML = '';
          this.$refs.componentPreview.appendChild(vm.$el);
        });
      }
      //
    }
  };
</script>

<style lang="scss" scoped>
  .fullScreen-page {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 99;
    width: 100vw;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.2);
    background-color: #fff;
    display: flex;
    padding: 10px;
    box-sizing: border-box;
    // 左
    .leftPanel {
      flex: 1;
      margin: 10px;
      padding: 10px;
      border: 1px solid #efeff5;
      display: flex;
      flex-direction: column;
      .panelHeader {
        display: flex;
        justify-content: flex-end;
        .btn {
          margin-left: 15px;
          padding: 6px 10px;
          border: 1px solid #adb0b8;
          border-radius: 4px;
          cursor: pointer;
          transition: all 0.3s;
          &:hover {
            color: #5e7ce0;
            border-color: #5e7ce0;
          }
        }
      }
      .codemirrorBox {
        margin-top: 15px;
        flex: 1;
        overflow: auto;
        // height: calc(100% - 40px);
        ::v-deep .vue-codemirror {
          height: 100%;
        }
        ::v-deep .CodeMirror {
          height: 100%;
        }
      }
    }
    // 右
    .rightPanel {
      flex: 1;
      margin: 10px;
      padding: 10px;
      border: 1px solid #efeff5;
      .previewBox {
        height: 400px;
        padding: 15px;
        // height: 100%;
        background-color: #051c4b;
      }
    }
  }
</style>

<style>
  .echart-unit {
    height: 100%;
  }
</style>
<style>
  .disabled-line {
    background-color: #f7f7f7 !important;
    color: #ccc !important;
    pointer-events: none !important;
  }
  .disabled-line-marker {
    background-color: #ccc;
    width: 10px;
    height: 10px;
    border-radius: 50%;
    margin-top: 2px;
  }
</style>
