// CodeMirror 定义 Vue 单文件的语言模式需要使用 `defineMode` 方法。这个方法用于定义一个新的语言模式，它的第一个参数是模式名称，第二个参数是一个返回 `TokenStream` 的函数，这个函数用于解析和高亮代码。

// 下面是一个定义 Vue 单文件语言模式的示例代码：
import * as CodeMirror from "codemirror/lib/codemirror";
import "codemirror/mode/vue/vue.js";
CodeMirror.defineMode("vue2", function(config, parserConfig) {
  // 定义模板解析器
  const templateMode = CodeMirror.getMode({}, "text/html");
  // 定义样式解析器
  const styleMode = CodeMirror.getMode({}, "text/css");
  // 定义脚本解析器
  const scriptMode = CodeMirror.getMode({}, "javascript");

  return {
    startState: function() {
      return {
        templateState: templateMode.startState(),
        styleState: styleMode.startState(),
        scriptState: scriptMode.startState()
      };
    },
    token: function(stream, state) {
      if (stream.match(/^<template>/)) {
        state.templateState = templateMode.startState();
        return "tag";
      } else if (stream.match(/^<\/template>/)) {
        state.templateState = null;
        return "tag";
      } else if (stream.match(/^<style(\s|>)/)) {
        state.styleState = styleMode.startState();
        return "tag";
      } else if (stream.match(/^<\/style(\s|>)/)) {
        state.styleState = null;
        return "tag";
      } else if (stream.match(/^<script(\s|>)/)) {
        state.scriptState = scriptMode.startState();
        return "tag";
      } else if (stream.match(/^<\/script(\s|>)/)) {
        state.scriptState = null;
        return "tag";
      } else if (state.templateState) {
        return templateMode.token(stream, state.templateState);
      } else if (state.styleState) {
        return styleMode.token(stream, state.styleState);
      } else if (state.scriptState) {
        return scriptMode.token(stream, state.scriptState);
      } else {
        stream.skipToEnd();
        return null;
      }
    }
  };
});

CodeMirror.defineMIME("text/vue2", "vue2");
